import React from 'react'
import { useState } from 'react';
import axios from 'axios';
import { Link, useParams  } from 'react-router-dom';
import Header from '../components/Header'
import Footer from '../components/Footer'
function PostView() {
  const {slug}=useParams();

  const baseUrl='https://bscriptsource.com/wp-json/wp/v2/posts?slug='+slug;
  console.log(baseUrl);
  const [Listb, setListb] = useState([]);
  const [Loading,setLoading]=useState(true);
  
  React.useEffect(() => {
    setLoading(true);
    axios.get(baseUrl).then((response) => {
      setListb(response.data);
      setLoading(false);
    });
    
  }, []);
  
  if (!Listb) return null;
  console.log(Listb)
  return (

 
    <React.Fragment>
    <Header/>
    
    <main id='main'>
    <section className="blog-wrapper sect-pt4 sect-mt4" id="blog">
  <div className="container">
    <div className="row">
      {
     
      Listb && Listb.map((post)=>{


return <div key={post.id}>

  <b>{post.title.rendered}</b> 
<div
      dangerouslySetInnerHTML={{__html: post.content.rendered}}
    />
    
{Loading ? <><div id="preloader"></div></> : <></> }
</div>
    

      })}

     
     
      </div></div>
      </section>
   </main>
    
    <Footer/>
    </React.Fragment>
  )
}

export default PostView
