import React from "react";
import Typed from 'react-typed';

function Herosection(){
  
return(
 <React.Fragment>  
    
  <div id="hero" className="hero route bg-image" style={{backgroundImage : "url(assets/images/hero-bg.jpg)"}}>
    <div className="overlay-itro"></div>
    <div className="hero-content display-table">
      <div className="table-cell">
        <div className="container">
          <h1 className="hero-title mb-4">I am Brijesh Paliwal</h1>
          <p className="hero-subtitle"><span className="typed" data-typed-items="Designer, Developer, Freelancer, Photographer">
                <Typed
                    strings={['Designer', 'Developer', 'Freelancer', 'Photographer']}
                    typeSpeed={40}
                    loop
                />
              </span></p>
         

          
        </div>
      </div>
    </div>
  </div>
  </React.Fragment>
)

}


export default Herosection;