import React, { Component } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom';

import Header from '../components/Header'
import Footer from '../components/Footer'

class PostList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      loading:false,
    };
    this.createMarkup = this.createMarkup.bind();
  }
  
  componentDidMount() { 
    this.setState({
      loading: true,       
    });
    axios.get('https://bscriptsource.com/wp-json/wp/v2/posts?_embed').then(posts => {
      this.setState({
        posts: posts.data,       
      });
      this.setState({
        loading: false,       
      });
    });
   
  }

  createMarkup(html) {
    return { __html: html };
  }

  render() {
    
    return (
   
        <React.Fragment>

        <Header/>
       
        <main id='main'>
    <section className="blog-wrapper sect-pt4 sect-mt4" id="blog">
  <div className="container">
  {this.state.loading ? <><div id="preloader"></div></> : <></> }
  
  

    <div className="row">
     <h1>Bscript post listing</h1>
        {this.state.posts.map(post => (
          <Link to={`/blog/${post.slug}`} key={post.id}>
            <div className='card mt-2 p-4' key={post.id}>
              <div className='card-content'>
                <h2>{post.title.rendered}
                
                
                </h2>
                <img
  alt="example"
  src={post._embedded['wp:featuredmedia'][0].media_details.sizes.thumbnail.source_url}
 />
                <div
                  dangerouslySetInnerHTML={this.createMarkup(
                    post.excerpt.rendered
                  )}
                />
              </div>
            </div>
          </Link>
        ))}
    </div></div></section></main>
    
      <Footer/>
      </React.Fragment>
    );
  }
}

export default PostList;