import React, { Component } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom';


class Homepost extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
      categoriesname:"",
      catid:''
    
    };
    this.createMarkup = this.createMarkup.bind();
  }
  
 /*   async getCategories(id) {
    await axios.get(`https://bscriptsource.com/wp-json/wp/v2/categories/${id}`).then(categoriesname => {
      this.setState({
        categoriesname : categoriesname.data.name

      })
    });
  }
  */
   fetchCategory = async (id) =>  {  
    await axios.get(`https://bscriptsource.com/wp-json/wp/v2/categories/${id}`).then(categoriesname => {
      this.setState({
        categoriesname : categoriesname.data.name

      })

      

    });
   // set your state or whatever
   }
   fetchPostlist = async () =>  {await axios.get('https://bscriptsource.com/wp-json/wp/v2/posts?_embed&per_page=3').then(posts => {
      this.setState({
        posts: posts.data,
        catid : posts.data[0].categories[0]

      })
      this.fetchCategory(posts.data[0].categories[0])
          }) 
         
        }

  componentDidMount() {
    this.fetchPostlist();
   
  }

  createMarkup(html) {
    return { __html: html };
  }

  render() {
   
       
    return (

        <React.Fragment>
               
        <section id="blog" className="blog-mf sect-pt4 route">
  <div className="container">
    <div className="row">
      <div className="col-sm-12">
        <div className="title-box text-center">
          <h3 className="title-a">
            Blog
          </h3>
          <p className="subtitle-a">
            Lorem ipsum, dolor sit amet consectetur adipisicing elit.
          </p>
          <div className="line-mf"></div>
        </div>
      </div>
    </div>
    <div className="row">
        {this.state.posts.map(post => (

          
<div className="col-md-4" key={post.id}>
<div className="card card-blog">
  <div className="card-img">
  <Link to={`/blog/${post.slug}`} ><img src={post._embedded['wp:featuredmedia'][0].media_details.sizes.thumbnail.source_url} alt="" className="img-fluid"/></Link>
  </div>
  <div className="card-body">
    <div className="card-category-box">
      <div className="card-category">
        <h6 className="category"> 
        {
        this.state.categoriesname
        }
              
        </h6>
      </div>
    </div>
    <h3 className="card-title"><Link to={`/blog/${post.slug}`} >{post.title.rendered}</Link></h3>
    
    <div className="card-description"
                  dangerouslySetInnerHTML={this.createMarkup(
                    post.excerpt.rendered
                  )}
                />
    
  </div>
  <div className="card-footer">
    <div className="post-author">
      <a href="#">
        <img src="assets/images/testimonial-2.jpg" alt="" className="avatar rounded-circle"/>
        <span className="author">Brijesh Paliwal</span>
      </a>
    </div>
    <div className="post-date">
      <span className="bi bi-clock"></span> 10 min
    </div>
  </div>
</div>
</div>



        ))}
        </div>
   </div></section>
      </React.Fragment>
    );
  }
}

export default Homepost;